/* content */


html {
    box-sizing: border-box;
    font-size: 62.5%; /* 1rem = 10px, 1.6rem = 16px ...*/
}

* {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    font-size: 1.6rem;
    font-family: 'Ubuntu Condensed', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* --- Login --- */
