}

.main-not-found__code {
    margin: 0 auto;
    font-size: 10rem;
    -webkit-animation: pulse 2.5s infinite both;
            animation: pulse 2.5s infinite both;
}
.main-not-found__btn {
    margin-top: 30px;
    padding: 6px 8px;
    color: var(--orange-dark);
    font-size: 1.8rem;
    font-weight: 600;
    letter-spacing: .02em;
    background-color: var(--white);
    border-radius: 8px;
    -webkit-animation: bounding .5s infinite both;
            animation: bounding .5s infinite both;
}

@-webkit-keyframes bounding {
    to { -webkit-transform: translateX(8px); transform: translateX(8px); }
    from { -webkit-transform: translateX(0); transform: translateX(0);}
}

@keyframes bounding {
    to { -webkit-transform: translateX(8px); transform: translateX(8px); }
    from { -webkit-transform: translateX(0); transform: translateX(0);}
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    20%, 60% {
        -webkit-transform: rotate(80deg);
                transform: rotate(80deg);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    40% {
        -webkit-transform: rotate(60deg);
                transform: rotate(60deg);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    80% { opacity: 0.5; }
    100% {
        -webkit-transform: translateY(400px);
                transform: translateY(400px);
        opacity: 0;
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    20%, 60% {
        -webkit-transform: rotate(80deg);
                transform: rotate(80deg);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    40% {
        -webkit-transform: rotate(60deg);
                transform: rotate(60deg);
        -webkit-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
    80% { opacity: 0.5; }
    100% {
        -webkit-transform: translateY(400px);
                transform: translateY(400px);
        opacity: 0;
    }
}


/* Media Queries */
@media only screen and (max-width: 600px) {
    @-webkit-keyframes pulse {
        0% {
            -webkit-transform: rotate(0);
                    transform: rotate(0);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        20%, 60% {
            -webkit-transform: rotate(80deg);
                    transform: rotate(80deg);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        40% {
            -webkit-transform: rotate(60deg);
                    transform: rotate(60deg);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        80% { opacity: 0.5; }
        100% {
            -webkit-transform: translateY(250px);
                    transform: translateY(250px);
            opacity: 0;
        }
    }
    @keyframes pulse {
        0% {
            -webkit-transform: rotate(0);
                    transform: rotate(0);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        20%, 60% {
            -webkit-transform: rotate(80deg);
                    transform: rotate(80deg);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        40% {
            -webkit-transform: rotate(60deg);
                    transform: rotate(60deg);
            -webkit-transform-origin: top left;
                    transform-origin: top left;
            -webkit-animation-timing-function: ease-in-out;
                    animation-timing-function: ease-in-out;
        }
        80% { opacity: 0.5; }
        100% {
            -webkit-transform: translateY(250px);
                    transform: translateY(250px);
            opacity: 0;
        }
    }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

:root{
    --blue: #3a84fa; 
    --white: #fff;
    --grey: #f5f5f5;
    --black1: #222;
    --black2: #999;
}

.body_Admin {
    min-height: 100vh;
    overflow-x: hidden;
}

.container_Admin{
    position: relative;
    width: 100%;
}

.close-icon__menu {
    width: 18px;
    height: 20px;
    margin-top: 10px;
    margin-right: 20px;
    z-index: 200;
}

.container_Admin .navigation{
    position: fixed;
    width: 250px;
    height: 100%;
    background: #3a84fa;
    background: var(--blue);
    border-left: 1px solid #3a84fa;
    border-left: 1px solid var(--blue);
    transition: 0.5s;
    overflow: hidden;
}

.navigation.active{
    width: 80px;
}

.navigation ul{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}

.navigation ul li{
    position: relative;
    width: 100%;
    list-style: none;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
}

.navigation ul li:hover,
.navigation ul li .hovered{
    background: #fff;
    background: var(--white);
}

.navigation ul li:nth-child(1) {
    margin-top: 10px;
    margin-bottom: 30px;
}

.navigation ul li a{
    position: relative;
    /*display: block;*/
    width: 100%;
    display: flex;
    text-decoration: none;
    color: #fff;
    color: var(--white);
}

.navigation ul li:hover a,
.navigation ul li .hovered a{
    color: #3a84fa;
    color: var(--blue);
}

.navigation ul li a .iconAdmin{
    position: relative;
    display: block;
    min-width: 50px;
    height: 50px;
    line-height: 60px;
    text-align: center;
}

.navigation ul li a .iconAdmin ion-icon{
    font-size: 1.3em;
}

.navigation ul li a .title{
    position: relative;
    display: block;
    padding: 0 10px;
    height: 52px;
    line-height: 50px;
    text-align: start;
    white-space: nowrap;
}

/* Curve outside */
.navigation ul li:hover a::before,
.navigation ul li .hovered a::before {
    content: '';
    position: absolute;
    right: 0;
    top: -50px;
    width: 50px;
    height: 50px;
    background: transparent;
    border-radius: 50%;
    box-shadow: 35px 35px 0 10px #fff;
    box-shadow: 35px 35px 0 10px var(--white);
    pointer-events: none;
}

.navigation ul li:hover a::after,
.navigation ul li .hovered a::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: -50px;
    width: 50px;
    height: 50px;
    background: transparent;
    border-radius: 50%;
    box-shadow: 35px -35px 0 10px #fff;
    box-shadow: 35px -35px 0 10px var(--white);
    pointer-events: none;
}

/* main */
.main{
    position: absolute;
    width: calc(100% - 250px);
    left: 250px;
    min-height: 100vh;
    background: #fff;
    background: var(--white);
    transition: 0.5s;
}

.main.active{
    width: calc(100% - 80px);
    left: 80px;
}

.topbar{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}

.toggle{
    position: relative;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5em;
    cursor: pointer;
}

.search{
    position: relative;
    width: 400px;
    margin: 0 10px;
}

.search label{
    position: relative;
    width: 100%;
}

.search label input{
    width: 100%;
    height: 40px;
    border-radius: 40px;
    padding: 5px 20px;
    padding-left: 35px;
    font-size: 18px;
    outline: none;
    border: 1px solid #999;
    border: 1px solid var(--black2);
}

.search label ion-icon {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 1.2em;
}

.user{
    position: relative;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;
}

.user img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.cardBox{
    position: relative;
    width: 100%;
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 30px;
}

.cardBox .cardAdmin{
    position: relative;
    background: #fff;
    background: var(--white);
    padding: 30px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
}

.cardBox .cardAdmin .numbers{
    position: relative;
    font-weight: 500;
    font-size: 2.1em;
    color: #3a84fa;
    color: var(--blue);
}

.cardBox .cardAdmin .cardName{
    color: #999;
    color: var(--black2);
    font-size: 1em;
    margin-top: 5px;
}

.cardBox .cardAdmin .iconBox{
    font-size: 2.8em;
    color: #999;
    color: var(--black2);
}

.cardBox .cardAdmin:hover {
    background: #3a84fa;
    background: var(--blue);
}

.cardBox .cardAdmin:hover .numbers,
.cardBox .cardAdmin:hover .cardName,
.cardBox .cardAdmin:hover .iconBox{
    color: #fff;
    color: var(--white);
}

.detailsAdmin{
    position: relative;
    width: 100%;
    padding: 20px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 30px;
    margin-top: 10px;
}

.detailsAdmin .recentOrders{
    position: relative;
    /*display: grid;
    min-height: 500px;*/
    background: #fff;
    background: var(--white);
    padding: 20px;
    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
}

.cardHeader{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.cardHeader h2{
    font-weight: 600;
    color: #3a84fa;
    color: var(--blue);
}

.cardHeader .btn{
    position: relative;
    padding: 5px 10px;
    background: #3a84fa;
    background: var(--blue);
    text-decoration: none;
    color: #fff;
    color: var(--white);
    border-radius: 6px;
}

.detailsAdmin table{
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.detailsAdmin table thead td{
    font-weight: 600;
}

.detailsAdmin .recentOrders table tr:last-child{
    border-bottom: none;
}

.detailsAdmin .recentOrders table tr{
    color: #222;
    color: var(--black1);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.detailsAdmin .recentOrders table tbody tr:hover{
    background: #3a84fa;
    background: var(--blue);
    color: #fff;
    color: var(--white);
}

.detailsAdmin .recentOrders table tr td{
    padding: 10px;
}

.detailsAdmin .recentOrders table tr td:last-child {
    text-align: end;
}

.detailsAdmin .recentOrders table tr td:nth-child(2){
    text-align: end;
}

.detailsAdmin .recentOrders table tr td:nth-child(3){
    text-align: center;
}

.status.delivered{
    padding: 2px 4px;
    background: #8de02c;
    color: #fff;
    color: var(--white);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
}

.status.inprogress{
    padding: 2px 4px;
    background: #1795ce;
    color: #fff;
    color: var(--white);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
}

.status.pending{
    padding: 2px 4px;
    background: #0ea13a;
    color: #fff;
    color: var(--white);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
}

.status.return{
    padding: 2px 4px;
    background: #f00;
    color: #fff;
    color: var(--white);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
}

.recentCustomers{
    position: relative;
    /*display: grid;
    min-height: 500px;*/
    padding: 20px;
    background: #fff;
    background: var(--white);
    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
}

.recentCustomers .imgBx{
    position: relative;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
}

.recentCustomers .imgBx img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.recentCustomers table tr:hover{
    background: #3a84fa;
    background: var(--blue);
    color: #fff;
    color: var(--white);
}

.recentCustomers table tr td{
    padding: 12px 10px;
}

.recentCustomers table tr td h4{
    font-size: 16px;
    font-weight: 500;
    line-height: 1.2em;
}

.recentCustomers table tr td h4 span{
    font-size: 14px;
    color: #999;
    color: var(--black2);
}

.recentCustomers table tr:hover td h4 span{
    color: #fff;
    color: var(--white);
}

/* responsive design */
@media (max-width: 991px){
    .navigation{
        left: -25px;
    }

    .navigation.active{
        width: 300px;
        left: 0;
    }

    .main{
        width: 100%;
        left: 0;
    }

    .main.active{
        left: 300px;
    }

    .cardBox{
        grid-template-columns: repeat(2,1fr);
    }
}

@media (max-width: 768px){
    .detailsAdmin{
        grid-template-columns: repeat(1,1fr);
    }

    .recentOrders{
        overflow-x: auto;
    }

    .status.inprogress{
        white-space: nowrap;
    }

}

@media (max-width: 480px){
    .cardBox{
        grid-template-columns: repeat(1,1fr);
    }

    .cardHeader h2{
        font-size: 20px;
    }

    .user{
        min-width: 40px;
    }

    .navigation{
        width: 100%;
        left: -100%;
        z-index: 1000;
    }

    .close-icon__menu {
        fill: #fff;
        fill: var(--white);
    }

    .navigation.active{
        width: 100%;
        left: 0;
    }

    .toggle{
        z-index: 10001;
    }

    .main.active .toggle{
        position: fixed;
        right: 0;
        left: auto;
        left: initial;
        color: #fff;
        color: var(--white);
    }

}
